.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

html {
  font-size: 18px;
  scroll-behavior: smooth; }

body {
  font-family: interstate, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff; }
  body .background-color {
    background-color: #000000;
    height: 2000vh; }
    body .background-color .background-svg {
      width: 100%;
      height: 100%; }
      body .background-color .background-svg svg {
        width: auto;
        height: 100%;
        fill: none;
        stroke: #7B0000;
        stroke-miterlimit: 10;
        stroke-width: 50px; }
        body .background-color .background-svg svg .blood-line {
          transition: all 2s cubic-bezier(0, 0, 0, 1);
          stroke-dashoffset: 14325.4; }
      body .background-color .background-svg .fluid-container {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%; }
        body .background-color .background-svg .fluid-container .container {
          max-width: 80vw;
          margin: 0 auto; }

section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

h1,
h2,
h3,
h4,
